/** @format */

import { Box } from '@mui/material';
import React from 'react';

const boxContainer = {
	backgroundColor: 'lightBlue',
	display: 'flex',
	flexDirection: 'row',
	flexWrap: 'wrap',
	flexGrow: 1,
	borderWidth: 20,
	borderStyle: 'solid',
	borderColor: 'purple',
	margin: 10,
	height: '90vh',
	// padding: 10,
	justifyContent: 'space-between',
};

const boxItem = {
	// margin: 5,
	backgroundColor: 'orange',
	borderWidth: 5,
	padding: 5,
};

function FlexTest(props) {
	return (
		<Box sx={boxContainer}>
			<Box sx={boxItem}>Item 1</Box>
			<Box sx={boxItem}>Item 2</Box>
			<Box sx={boxItem}>Item 3</Box>
		</Box>
	);
}

export default FlexTest;
